<template>
    <div>
      <div class="centerTitle">
        新建广告
      </div>

      <div class="totalTableSize">
        <div class="tipIcon"></div>
        <div class="totalCon">
          进行广告位广告来源的配置：通过此功能可以动态切换各广告位展示的广告来源，从而可以根据不同SSP平台的佣金多寡、类型实现动态调整
        </div>
      </div>
      <div class="formCon">
        <div class="formItem">
          <div class="label">广告名称：</div>
          <div class="input">
            <el-input v-model="name" placeholder="给广告起个名字"></el-input>
          </div>
        </div>
        <div class="formItem">
          <div class="label">广告位名称：</div>
          <div class="input">
            <el-select v-model="status" placeholder="请选择">
              <el-option
                v-for="item in selectOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="formItem">
          <div class="label">广告类型：</div>
          <div class="input">
            <el-select v-model="status" placeholder="请选择">
              <el-option
                v-for="item in selectOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="formItem">
          <div class="label">广告来源：</div>
          <div class="input">
            <el-checkbox-group v-model="checkList">
              <el-checkbox label="复选框 A"></el-checkbox>
              <el-checkbox label="复选框 B"></el-checkbox>
              <el-checkbox label="复选框 C"></el-checkbox>
            </el-checkbox-group>
          </div>
        </div>
        <div class="formItem">
          <div class="label">佣金：</div>
          <div class="input numberInput">
            <el-input-number v-model="num" controls-position="right" :min="0"></el-input-number>
          </div>
          <div class="numberText">金币</div>
        </div>
        <div class="formItem">
          <div class="label">数量：</div>
          <div class="input numberInput">
            <el-input-number v-model="num" controls-position="right" :min="0"></el-input-number>
          </div>
          <div class="numberText">个</div>
        </div>
        <div class="formBtnGroup">
          <el-button type="primary" class="CoBtn" @click="saveForm()">保存</el-button>
          <el-button class="CoResetBtn" @click="goPrePage()">返回上一页</el-button>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
      name: "AddPoster",
      data(){
        return{
          name : '',
          status : '',
          num : '',
          selectOptions: [
            {
              value: '1',
              label: '上架'
            }, {
              value: '2',
              label: '下架'
            }
          ],
          checkList:[]
        }
      }
    }
</script>

<style lang="css" scoped>
  .totalCon{
    width: 95%;
  }
</style>



